<template>
    <div class="element" itemscope itemtype="http://schema.org/Thing">
        <div class="head" itemprop="name">
            <img target="blank" class="image" src="/img/persons/default.webp" itemprop="image" @click="expand = !expand">
            <div class="info" @click="expand = !expand">
                <div class="level">
                    {{levels[level]}}
                </div>
                <span class="name">{{name}} <i v-if="id == currentUser.id">(Vous)</i></span>
            </div>
            <div class="content" v-if="expand">
                <small>Contact :</small> {{contact}}
                <small>Inscrit le :</small> {{created_at}}
                
            </div>
            <div class="expand">
                <img src="@/assets/icon/down.svg" :class="{'on': expand}" @click="expand = !expand">
            </div>
        </div>
        <div class="content" v-if="expand && showActions">
            <div class="levels" v-if="id != currentUser.id">
                <div :class="['option', level == getSelected?'selected':'']" v-for="level in levels" :key="levels.indexOf(level)" @click="select(level)">{{level}}</div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    props: ['id', 'name','contact', 'level', 'created_at', 'actions'],
    data () {
        return {
            expand: false,
            levels: ['Non-validé', 'Utilisateur', 'Rédacteur', 'Administrateur'],
            selected: null
        }
    },
    computed: {
        ...mapGetters('jwt',[
            'jwt'
        ]),
        ...mapGetters('users',[
            'currentUser'
        ]),
        showActions: function () {
            return this.jwt && this.actions == "true"
        },
        getSelected: function() {
            return this.selected?this.selected:this.levels[this.level]
        }
    },
    methods: {
        ...mapActions('users',[
            `modifyUser`
        ]),
        select(value) {
            this.selected = value;
            this.modifyUser({
                id: this.id,
                name: null,
                contact: null,
                level: this.levels.indexOf(value)
            });
        }
    }
}
</script>
<style scoped>
.levels {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin: 10px 0;
}
.option, .delete {
    background: var(--b8);
    user-select: none;
    display: flex;
    align-self: center;
    width: auto;
    cursor: pointer !important;
    margin: 5px;
    padding: 7px 12px 7px 7px;
    border-radius: 7px;
}
.option.selected {
    background: var(--b3);
    color: var(--b8);
}
</style>