<template>
    <div>
        <main>
        <div class="title">
            <h1>Tableau de bord</h1>
        </div>
        <div class="card content" v-if="!classed">
            <button @click="$router.push('/dashboard')">Retour</button>
            <button class="outline" @click="classed= true">Classer par niveau</button>
            <h2>Utilisateurs</h2>
            <BarLoader style="margin: 0 auto; border-radius:5px" v-if="usersLoading"></BarLoader>
            <div class="users" v-else>
                <User v-for="user in users" v-bind="user" :key="user.id" actions="true"/>
            </div>
        </div>
        <div class="card content" v-else>
            <button>Retour</button>
            <button class="outline" @click="classed= false">Ne pas classer</button>
            <h2>Non-validés</h2>
            <BarLoader style="margin: 0 auto; border-radius:5px" v-if="usersLoading"></BarLoader>
            <div class="users" v-else>
                <User v-for="user in usersNoValidated" v-bind="user" :key="user.id" actions="true"/>
                <span v-if="usersNoValidated.length == 0">Aucun utilisateur</span>
            </div>
            <h2>Utilisateurs</h2>
            <BarLoader style="margin: 0 auto; border-radius:5px" v-if="usersLoading"></BarLoader>
            <div class="users" v-else>
                <User v-for="user in usersUser" v-bind="user" :key="user.id" actions="true"/>
                <span v-if="usersUser.length == 0">Aucun utilisateur</span>
            </div>
            <h2>Rédacteurs</h2>
            <BarLoader style="margin: 0 auto; border-radius:5px" v-if="usersLoading"></BarLoader>
            <div class="users" v-else>
                <User v-for="user in usersModo" v-bind="user" :key="user.id" actions="true"/>
                <span v-if="usersModo.length == 0">Aucun utilisateur</span>
            </div>
            <h2>Administrateurs</h2>
            <BarLoader style="margin: 0 auto; border-radius:5px" v-if="usersLoading"></BarLoader>
            <div class="users" v-else>
                <User v-for="user in usersAdmin" v-bind="user" :key="user.id" actions="true"/>
                <span v-if="usersAdmin.length == 0">Aucun utilisateur</span>
            </div>
        </div>
        </main>
    </div>
</template>
<script>
import User from '@/components/card/user.vue'
import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'home',
    components: {
        User
    },
    data () {
        return{
            classed: false
        }
    },
    created () {
        this.fetchUsers()
    },
    computed: {
        ...mapGetters('users',[
            'usersLoading',
            'users',
            'usersNoValidated',
            'usersUser',
            'usersModo',
            'usersAdmin'
        ])
    },
    methods: {
        ...mapActions('users',[
            'fetchUsers'
        ]),
    }
}
</script>
<style scoped>
h2 {
    margin-top: 50px;
}
button {
    outline: none;
    transition: all .3s;
    user-select: none;
    cursor: pointer;
    display: inline-block;
    border-radius: 7px;
    border: none;
    font-size: 15px;
    background: var(--b3);
    border: 1px solid var(--b3);
    color: var(--b8);
    margin: 10px 15px 0 0;
    padding: 10px 16px;
}

button.outline {
    background: none;
    color: var(--b3);
}
button:hover { 
    border: 1px solid var(--b2);
    background: var(--b2);
}
button.outline:hover { 
    border: 1px solid var(--b3);
    background: var(--b3);
    color: var(--b8);
}
  
</style>